import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Theme,
  WithStyles,
  withStyles,
  Stepper,
  Step,
  StepLabel,
  StepContent,
  Button,
  Typography,
} from "@material-ui/core";
import { ICourse } from "../../../_types/course.types";
import { pathFinder, PathFinder } from "../../../_helpers/constants";

const useStyles = (theme: Theme) => ({
  root: {
    width: "250px",
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
});

interface SongListProps extends WithStyles<typeof useStyles> {
  course?: ICourse;
  lessonId?: number;
  pathname: string;
  requestLesson: (path: PathFinder) => void;
}
interface Isteps {
  id: number;
  name: string;
  excerpt: string;
  type: string;
}
interface States {
  lessonIndex: number;
  loading: boolean;
  steps: Isteps[];
  selected: number;
}
type Props = SongListProps;

class SongListSelector extends Component<Props, States> {
  constructor(props: Props) {
    super(props);
    this.state = {
      lessonIndex: 0,
      loading: true,
      steps: [],
      selected: 0,
    };
  }
  componentDidMount() {
    const path = pathFinder(this.props.pathname);
    if (path.course === this.props.course?.id) {
      const courses = this.props.course?.course_select;
      if (courses) {
        const steps: Isteps[] = [];
        let index = 0;
        courses.forEach((step) => {
          const type = step.info.genre.id === 128 ? "exercise" : "lesson";
          let excerpt = step.excerpt;
          if (!excerpt) {
            excerpt = "No Detail Provided";
          }
          steps.push({ id: step.id, name: step.title, excerpt, type });
        });
        index = steps.findIndex((step) => step.id === this.props.lessonId);
        this.setState({
          steps,
          loading: false,
          lessonIndex: index,
        });
      }
    }
  }

  linkGenerator = (type: string, id: number): string => {
    const linkElement = this.props.pathname.split("/").slice(0, -1);
    return `${linkElement.join("/")}/${type}=${id}`;
  };

  handleNext = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    const index = event.currentTarget.tabIndex;
    this.setState({ lessonIndex: index });
  };

  selectedLesson = (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => {
    const path = pathFinder(event.currentTarget.pathname);
    this.props.requestLesson(path);
  };

  render() {
    const { lessonIndex, loading, steps } = this.state;
    const { classes } = this.props;
    if (loading) {
      return <div>...</div>;
    } else {
      return (
        <div className={classes.root}>
          <Stepper activeStep={lessonIndex} orientation="vertical">
            {steps.map((label, i) => (
              <Step key={label.id}>
                <StepLabel tabIndex={i} onClick={this.handleNext}>
                  {label.name}
                </StepLabel>
                <StepContent>
                  <Typography>{label.excerpt}</Typography>
                  <Button
                    component={Link}
                    variant="contained"
                    color="secondary"
                    to={this.linkGenerator(label.type, label.id)}
                    className={classes.button}
                    onClick={this.selectedLesson}
                  >
                    Learn
                  </Button>
                </StepContent>
              </Step>
            ))}
          </Stepper>
        </div>
      );
    }
  }
}

export default withStyles(useStyles)(SongListSelector);
